.header__logo {
  max-width: 142px;
  height: 33px;
  margin: 45px 0 36px;
  padding: 0;
}

@media screen and (max-width: 1027px) {
  .header__logo {
    padding-left: 27px;
    margin: 28px 0 30px;
  }
}

@media screen and (max-width: 500px) {
  .header__logo {
    max-width: 103px;
    height: 24px;
  }
}
